$availableScreenHeight: calc(100vh - 32px); // screen height - height of breadcrumb

.worldMap {
  @apply w-screen min-w-fit lg:w-full;
  @apply overflow-y-clip;
  min-height: $availableScreenHeight;
  
  .mapWrap {
    @apply px-1 xl:px-0 mx-auto;
  }

  .toolTip {
    @apply fixed z-10 hidden;
    margin-top: -4.6rem;
    margin-left: -1.8rem;
    // @apply bg-sky-900 bg-opacity-50;
    @apply bg-stone-800 bg-opacity-60 shadow-2xl;
    @apply px-3 p-1 shadow rounded-full;
    // @apply text-xs text-white tracking-wide;
    @apply text-xs text-white tracking-wide;
  }

  svg {
    @apply w-full;
    min-height: $availableScreenHeight;
    max-height: $availableScreenHeight;
    // Mobiles browsers has touch gestures that perform actions at
    // the browser level, such as scrolling and zooming the viewport.
    // By setting touch-action: none you are telling the browser to
    // not perform those browser-level actions on the element and
    // instead trigger Pointer Events.
    @apply touch-none;
  }

  g {
    // transition: transform 0.1s ease-in-out;
  }

  path {
    @apply fill-stone-300 stroke-black;
    // fill-opacity: 0.7;
    stop-opacity: 1;
    stroke-width: 0.1;
    
    &:hover {
      @apply fill-stone-500;
      fill-opacity: 0.6;
    }
  }

  .visited {
    @apply fill-emerald-500;
  }

  .mapControlsWrap {
    @apply w-56 h-16;
    @apply z-30 fixed bottom-5 left-1/2 -ml-28;
    @apply bg-stone-900 bg-opacity-50;
    @apply shadow-xl rounded-full;
    @apply flex flex-row items-center justify-evenly;
  }
  
  .mapControlButton {
    @apply text-xs w-fit p-2 px-3;
    @apply bg-gradient-to-b from-slate-50 to-slate-200 opacity-80 lg:opacity-70;
    @apply shadow rounded-full;
    @apply lg:hover:opacity-100;
    @apply active:opacity-100 lg:active:animate-ping;
  }
}
